/* App component CSS */
.App {
  background-color: rgba(234, 236, 240, 1);
  height: 100vh;
  width: 100vw;
  display: flex;
  flex-direction: row;
}

.ScreenContainer {
  height: 100%;
  width: 100%;
  background-color: white;
  display: flex;
  flex-direction: column;
}
/* App component CSS */
/**/
/**/
/**/
/**/
/**/
/* Top Component CSS */
.Topbar {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  background-color: rgba(247, 132, 22, 1);
  /* border-bottom: 2px solid rgba(22, 120, 253, 1); */
  height: 46px;
}

.Topbar div:first-child {
  position: relative;
  left: 8px;
}

.Topbar div:last-child {
  display: flex;
  flex-direction: row;
  gap: 10px;
  position: relative;
  right: 3ch;
}

.Topbar div:last-child p {
  width: 40px;
  height: 16px;
  color: white;
  font-family: arial;
  font-weight: 400;
  font-size: 14px;
  line-height: 16px;
}
/* Top Component CSS */
