/**/
/**/
/**/
/* Table Component CSS */
.Table-Maindiv {
  height: max(310px, 60%);
  width: min(1500px, 96%);
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  gap: 1%;
  z-index: 11;
  /* overflow: visible;
  z-index: 0; */
}

.Table-div {
  /* overflow-x: auto;
  overflow-y: scroll; */
  width: 100%;
  height: 100%;
  overflow-x: auto;
  overflow-y: auto;
  align-items: center;
}

table {
  width: 98%;
  margin-left: 1%;
  border-collapse: collapse;
}

thead {
  color: white;
  display: flex;
  background-color: rgba(247, 132, 22, 1);
}

table tr {
  background-color: rgba(252, 244, 237, 1);
  border-collapse: collapse;
  display: flex;
}

table tr:nth-child(odd) {
  background-color: white;
}

th,
td {
  border-collapse: collapse;
  border: 1px solid rgba(255, 222, 190, 1);
}

th {
  height: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
}

table tr td:nth-child(1) {
  flex: 2;
  text-align: center;
}
table tr td:nth-child(2) {
  flex: 4;
  text-align: center;
}
table tr td:nth-child(3) {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
}
table tr td:nth-child(4) {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  text-wrap: wrap;
  min-width: 8ch;
}
table tr td:nth-child(5) {
  flex: 2;
  text-align: center;
}
table tr td:nth-child(6) {
  flex: 2;
  display: flex;
}
table tr td:nth-child(6) div {
  align-self: center;
  display: flex;
  justify-content: center;
  width: 100%;
  gap: 15px;
}
table tr td:nth-child(6) div button {
  position: relative;
  width: 18px;
  height: 18px;
  border-style: none;
  background-color: transparent;
  cursor: pointer;
}
/* Table Component CSS */
