/* ReadChat Component CSS */
.readchat {
  height: 100%;
  width: 100%;
  box-sizing: border-box;
  display: flex;
  align-items: center;
  gap: 5%;
  position: relative;
  left: 35%;
}

@media (max-width: 705px) {
  .readchat {
    /* justify-content: flex-start; */
    left: 5%;
  }
}
/* ReadChat Component CSS */
