/**/
/* Pagination Component CSS */
.Pagination {
  width: 100%;
  height: 10%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.paginationbtn {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  height: 100%;
}

.paginationbtn div {
  border: 1px solid rgba(224, 224, 224, 1);
  background-color: rgba(224, 224, 224, 1);
  box-sizing: border-box;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  color: rgba(119, 119, 119, 1);
}

.paginationbtn div:nth-child(1) {
  width: 76px;
  height: 30px;
}

.paginationbtn div:nth-child(2) {
  width: 32px;
  height: 32px;
  background-color: rgba(247, 132, 22, 1);
  font-weight: 600;
  color: white;
}
.paginationbtn div:nth-child(3) {
  width: 51px;
  height: 30px;
}
/* Pagination Component CSS */
