/**/
/**/
/**/
/**/
/* Chatpage Component CSS */
.Chatpage {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  height: 100%;
}

.Section1 {
  width: 100%;
  height: min(100px, 20%);
  display: flex;
  flex-direction: row;
}

.Section1 .addnewbtn {
  width: 120px;
  height: 38px;
  position: relative;
  right: 80px;
  background-color: rgba(247, 131, 22);
  border-style: none;
  color: white;
}

@media (max-width: 460px) {
  .Section1 .addnewbtn {
    right: 10px;
  }
}
/* Chatpage Component CSS */
