/**/
/**/
/* Upload Component CSS */
.Upload-Maindiv {
  height: min(124px, 25%);
  width: min(1500px, 96%);
  border-bottom: 1px solid black;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  overflow: visible;
  z-index: 200;
}

.Upload-Maindiv p:first-child {
  height: max(16px, 20%);
  margin: 0;
  font-family: Helvetica;
  font-weight: 700;
  font-size: 14px;
  line-height: 16px;
}

.Upload-Subdiv {
  height: 80%;
  display: flex;
  align-items: center;
  gap: 5%;
  overflow: visible;
}

.Upload-select {
  overflow: visible;
}

.DragDrop {
  display: flex;
  flex-direction: row;
  width: 40%;
  height: 70%;
  border: 1px dashed rgba(247, 132, 22, 1);
  text-align: center;
  justify-content: center;
  align-items: center;
}

.Upload-btn {
  width: min(153px, 30%);
  height: min(38px, 40%);
  background-color: rgba(247, 132, 22, 1);
  text-align: center;
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}

.Dropdown-menu {
  display: block;
  border: 10px solid red;
}
/* Upload Component CSS */
